.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.centered {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.prod-body {
  margin: auto;
  background: rgb(152, 245, 252);
  width: 100%;
  height: 100%;
}

.back-pic {
  background-repeat: repeat-x;
}

.card {
  margin-top:  2em;
  margin-left: 2em;
  width: 16rem;
  height: 30rem;
  background-color: darkgray;
}

.price {
  margin-left: 1rem ;
  font-style: bold;
}

.drop {
  color: white;
}